	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>senior Registered Liberals across Canada identified through the 2017 SLC PolicyQuestionnaire the need for “government leadership in addressing all aspects ofenvironmental sustainability, including air, water, and land, not just climate change andnot just mitigation” as a priority issue for consideration during the next federal electioncampaign;</p>\n</li>\n<li>\n<p>the federal government has prioritized reducing the impact of greenhouse gas emissionson the global climate;</p>\n</li>\n<li>\n<p>numerous studies, such as the Brundtland report titled “Our Common Future”, suggestthat the current pace and nature of global economic activity is not environmentallysustainable ;</p>\n</li>\n<li>\n<p>other studies, such as Dietz and O’Neil’s hypothesis contained in “Enough is Enough-Building a Sustainable Economy in a World of Finite Resources”, suggest that a largerproportion of the global population could achieve a middle-class lifestyle with a reducedimpact on the environment;</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> that the Liberal Party of Canada urge the Government of Canada:</p>\n<ul>\n<li>\n<p>to accept that it must provide active leadership to set and achieve goals for long termenvironmental sustainability, including air, water, land, habitat, and speciesdiversification in addition to climate change;</p>\n</li>\n<li>\n<p>to recognize that such leadership will require changes in individual, corporate andgovernment behaviour and attitudes;</p>\n</li>\n<li>\n<p>to work urgently with individual, corporate and indigenous stakeholders to identify andimplement new economic development models that better balance environmentalsustainability with economic activity(Seniors Commission)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/environmental-sustainability","date":"Mon Mar 05 2018 23:27:04 GMT-0500 (EST)","title":"Environmental Sustainability"}}},"pathContext":{}}

